import { WidgetBase } from '@dojo/widget-core/WidgetBase';
import { v, w } from '@dojo/widget-core/d';
import { theme, ThemeableMixin } from '@dojo/widget-core/mixins/Themeable';
import { Link } from '@dojo/routing/Link';

import * as css from './styles/article.m.css';

interface ArticleProperties {
	item: any;
	index: number;
	pageNumber: number;
}

@theme(css)
export class Article extends ThemeableMixin(WidgetBase)<ArticleProperties> {

	render() {
		const { item, index, pageNumber } = this.properties;

		return v('article', { classes: this.classes(css.root) }, [
			v('span', { classes: this.classes(css.pageNumber) }, [ `${(pageNumber - 1) * 30 + index + 1}` ]),
			v('div', { key: index, classes: this.classes(css.post) }, [
				v('h2', [
					v('a', { href: item.url, target: 'none', classes: this.classes(css.title) }, [ item.loading ? 'Loading...' : item.title ])
				]),
				v('p', { classes: this.classes(css.details) }, [
					!item.loading ? `${item.points} points by ` : '',
					w(Link, { key: 'user', to: 'user', params: { user: item.user || ''}, classes: this.classes(css.link) }, [
						item.loading ? '' : item.user
					]),
					!item.loading ? ` ${item.time_ago} ` : '',
					w(Link, { key: 'comments', to: 'comments', params: { id: `${item.id}` || '' }, classes: this.classes(css.link) }, [
						item.loading ? '' : `${item.comments_count} comments`
					])
				])
			]),
		]);
	}

}



